import { useState } from 'react';
import './create_team.css'

import EndzoneShieldLogo from './images/Endzone_Shield.png'
import EndzoneButton from '../../../components/buttons/endzone-new-button/endzone-button' 
import AddIcon from '@mui/icons-material/Add';
import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

function CreateTeam() {
    const [teamName, setTeamName] = useState('');
    const [competitionLevel, setCompetitionLevel] = useState('');
    const [address, setAddress] = useState('');
    const [city, setCity] = useState('');
    const [zipCode, setZipCode] = useState('');
    const [state, setState] = useState('');
    const httpHandler = require('react-http-client');
    const [alert, updateAlert] = useState({
        style: {display: "none"},
        severity: "info",
        userMessage: ""
    })

    function closeAlert(){
        updateAlert({
                style: {display: "none"},
                severity: "info",
                userMessage: "" 
            }
        )
    }

    function handleError(request){
        try {   
            var response = JSON.parse(request.message)
            updateAlert({
                style: {display: "block"},
                severity: "error",
                userMessage: response.data
            })
        }
        catch {
            updateAlert({
                style: {display: "block"},
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            })
        }
    }


    function loginFail(message_str){
        document.getElementById("userMessage").innerText = message_str
    }

    function sendCreateRequest(e) {
        const request = httpHandler.post(
            "/account/team/create", 
            {
                "teamName": teamName,  
                "competitionLevel": competitionLevel, 
                "address": address,
                "city": city,
                "zipCode": zipCode,
                "state": state
            }   
        ).then(
            (request) => {
                window.location.href = "/endzone/hub"
            }, 
            (request) => {
                handleError(request)
            }
        );
    }

    function handleSubmit(e) {
        sendCreateRequest(e)
    }

    function logoDivClick() {
        window.location.href = "/";
    }

    return (
        <div>
            <div className='alert-div' style = {alert.style}>
                <Alert variant="filled" severity={alert.severity}
                            action={
                                <IconButton
                                aria-label="close"
                                color="inherit"
                                size="small"
                                onClick={() => {
                                    closeAlert();
                                }}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {alert.userMessage}
                </Alert>
            </div>
            <div className = "create-team-container">
                <div className = 'logoDiv' id = 'logoDiv'>
                        <img src={EndzoneShieldLogo} class = "logo" alt="Endzone Logo" onClick={() => logoDivClick()}></img>
                </div>
                <form className="register-form" onSubmit={handleSubmit}>
                    <label className="input-title" htmlFor="teamName">Team Name</label>
                    <input className="input-box" value={teamName} onChange={(e) => setTeamName(e.target.value)}/>
                    
                    <label className="input-title" htmlFor="competitionLevel">Competition Level</label>
                    <select className="select-input" value={competitionLevel} onChange={(e) => setCompetitionLevel(e.target.value)}>
                        <option default>Click here to select</option>
                        <option value="Youth">Youth</option>
                        <option value="High School">High School</option>
                        <option value="College">College</option>
                        <option value="Professional">Professional</option>
                    </select>

                    <label className="input-title" htmlFor="address">Street Address</label>
                    <input className="input-box" value={address} onChange={(e) => setAddress(e.target.value)}/>

                    <label className="input-title" htmlFor="city">City</label>
                    <input className="input-box" value={city} onChange={(e) => setCity(e.target.value)}/>

                    <label className="input-title" htmlFor="zipCode">Zip Code</label>
                    <input className="input-box" type="numeric" maxLength={5} value={zipCode} onChange={(e) => setZipCode(e.target.value)}/>

                    <label className="input-title" htmlFor="state">State</label>
                    <select className="select-input" value={state} onChange={(e) => setState(e.target.value)}>
                        <option default>Click here to select</option>
                        <option value="AL">AL</option>
                        <option value="AK">AK</option>
                        <option value="AZ">AZ</option>
                        <option value="AR">AR</option>
                        <option value="CA">CA</option>
                        <option value="CO">CO</option>
                        <option value="CT">CT</option>
                        <option value="DE">DE</option>
                        <option value="DC">DC</option>
                        <option value="FL">FL</option>
                        <option value="GA">GA</option>
                        <option value="GU">GU</option>
                        <option value="HI">HI</option>
                        <option value="ID">ID</option>
                        <option value="IL">IL</option>
                        <option value="IN">IN</option>
                        <option value="IA">IA</option>
                        <option value="KS">KS</option>
                        <option value="KY">KY</option>
                        <option value="LA">LA</option>
                        <option value="ME">ME</option>
                        <option value="MD">MD</option>
                        <option value="MA">MA</option>
                        <option value="MI">MI</option>
                        <option value="MN">MN</option>
                        <option value="MS">MS</option>
                        <option value="MO">MO</option>
                        <option value="MT">MT</option>
                        <option value="NE">NE</option>
                        <option value="NV">NV</option>
                        <option value="NH">NH</option>
                        <option value="NJ">NJ</option>
                        <option value="NM">NM</option>
                        <option value="NY">NY</option>
                        <option value="NC">NC</option>
                        <option value="ND">ND</option>
                        <option value="OH">OH</option>
                        <option value="OK">OK</option>
                        <option value="OR">OR</option>
                        <option value="PA">PA</option>
                        <option value="PR">PR</option>
                        <option value="RI">RI</option>
                        <option value="SC">SC</option>
                        <option value="SD">SD</option>
                        <option value="TN">TN</option>
                        <option value="TX">TX</option>
                        <option value="UT">UT</option>
                        <option value="VT">VT</option>
                        <option value="VI">VI</option>
                        <option value="VA">VA</option>
                        <option value="WA">WA</option>
                        <option value="WV">WV</option>
                        <option value="WI">WI</option>
                        <option value="WY">WY</option>
                    </select>

                    <div className = "button-container">
                        <div className = "button-container">
                            <EndzoneButton id = "log-in-btn" style={{"margin": "2vh"}} text = "Create Team" onClick = {handleSubmit} icon = {<AddIcon sx = {{"font-size": '150%'}} ></AddIcon>}></EndzoneButton>
                        </div>
                    </div>
                    <p id = "userMessage"></p>
                </form>
            </div> 
        </div>
    );
}

export default CreateTeam;
